/**
 * @fileoverview This file defines the page for adding a new employee.
 * It provides a form for HR personnel to enter the details of a new employee.
 */
'use client';

import * as React from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
  CardFooter,
} from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { ArrowLeft, UserPlus } from 'lucide-react';
import Link from 'next/link';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import {
    AlertDialog,
    AlertDialogAction,
    AlertDialogCancel,
    AlertDialogContent,
    AlertDialogDescription,
    AlertDialogFooter,
    AlertDialogHeader,
    AlertDialogTitle,
    AlertDialogTrigger,
} from "@/components/ui/alert-dialog";
import { useRouter } from 'next/navigation';
import { useToast } from '@/hooks/use-toast';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { addEmployee } from './actions';

const employeeSchema = z.object({
    firstName: z.string().min(1, "First name is required."),
    lastName: z.string().min(1, "Last name is required."),
    middleName: z.string().optional(),
    gender: z.string().min(1, "Gender is required."),
    dob: z.string().min(1, "Date of birth is required."),
    position: z.string().min(1, "Position is required."),
    department: z.string().min(1, "Department is required."),
    branch: z.string().min(1, "Branch is required."),
    hireDate: z.string().min(1, "Date of hire is required."),
    email: z.string().email("Invalid email address."),
    phone: z.string().min(1, "Phone number is required."),
    photo: z.any().optional(),
});

/**
 * Renders the form to add a new employee.
 * @returns {JSX.Element} The add employee page component.
 */
export default function AddEmployeePage() {
  const [photoPreview, setPhotoPreview] = React.useState(null);
  const router = useRouter();
  const { toast } = useToast();

  const form = useForm({
    resolver: zodResolver(employeeSchema),
    defaultValues: {
        firstName: '',
        lastName: '',
        middleName: '',
        gender: '',
        dob: '',
        position: '',
        department: '',
        branch: '',
        hireDate: '',
        email: '',
        phone: '',
        photo: null,
    }
  });


  const handlePhotoChange = (event) => {
    const file = event.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setPhotoPreview(reader.result);
      };
      reader.readAsDataURL(file);
      form.setValue('photo', file);
    } else {
      setPhotoPreview(null);
      form.setValue('photo', null);
    }
  };

  const handleSubmit = async (values) => {
    const formData = new FormData();
    for (const key in values) {
      formData.append(key, values[key]);
    }
    // We can add photo handling here if needed, e.g., uploading to storage
    // and appending a URL to formData. For now, we pass the data.

    const result = await addEmployee(formData);

    if (result.success) {
        toast({
            title: 'Success',
            description: 'New employee has been submitted for approval.',
        });
        router.push('/hr/employee-data');
    } else {
        toast({
            variant: 'destructive',
            title: 'Error',
            description: result.message || 'Failed to submit employee for approval.',
        });
    }
  };

  return (
    <div className="space-y-6">
      <div>
        <Button asChild variant="outline" size="sm">
          <Link href="/hr/employee-data">
            <ArrowLeft className="mr-2" />
            Back to Employee List
          </Link>
        </Button>
      </div>

    <Form {...form}>
      <form onSubmit={form.handleSubmit(handleSubmit)}>
        <Card className="max-w-4xl mx-auto">
            <CardHeader>
            <CardTitle className="flex items-center gap-2 text-2xl font-headline">
                <UserPlus /> Add New Employee
            </CardTitle>
            <CardDescription>
                Fill out the form below to add a new employee profile to the system.
            </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
            {/* Photo Upload Section */}
            <div className="space-y-4">
                <h3 className="text-lg font-medium">Employee Photo</h3>
                <div className="flex items-center gap-6">
                <Avatar className="h-24 w-24">
                    <AvatarImage src={photoPreview ?? undefined} alt="Employee photo preview" />
                    <AvatarFallback>
                    <UserPlus />
                    </AvatarFallback>
                </Avatar>
                <div className="space-y-2">
                    <Label htmlFor="photo-upload">Upload a photo</Label>
                    <Input id="photo-upload" type="file" accept="image/*" onChange={handlePhotoChange} />
                    <p className="text-xs text-muted-foreground">
                    PNG, JPG, or GIF up to 5MB. This photo will be used for facial recognition.
                    </p>
                </div>
                </div>
            </div>
            
            {/* Personal Information Section */}
            <div className="space-y-4">
                <h3 className="text-lg font-medium">Personal Information</h3>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <FormField
                    control={form.control}
                    name="firstName"
                    render={({ field }) => (
                    <FormItem>
                        <FormLabel>First Name</FormLabel>
                        <FormControl><Input placeholder="Michael" {...field} /></FormControl>
                        <FormMessage />
                    </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="lastName"
                    render={({ field }) => (
                    <FormItem>
                        <FormLabel>Last Name</FormLabel>
                        <FormControl><Input placeholder="Johnson" {...field} /></FormControl>
                        <FormMessage />
                    </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="middleName"
                    render={({ field }) => (
                    <FormItem>
                        <FormLabel>Middle Name</FormLabel>
                        <FormControl><Input placeholder="Lee" {...field} /></FormControl>
                        <FormMessage />
                    </FormItem>
                    )}
                />
                </div>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <FormField
                        control={form.control}
                        name="gender"
                        render={({ field }) => (
                        <FormItem>
                            <FormLabel>Gender</FormLabel>
                            <Select onValueChange={field.onChange} defaultValue={field.value}>
                                <FormControl>
                                    <SelectTrigger><SelectValue placeholder="Select gender" /></SelectTrigger>
                                </FormControl>
                                <SelectContent>
                                    <SelectItem value="male">Male</SelectItem>
                                    <SelectItem value="female">Female</SelectItem>
                                    <SelectItem value="other">Other</SelectItem>
                                </SelectContent>
                            </Select>
                            <FormMessage />
                        </FormItem>
                        )}
                    />
                    <FormField
                        control={form.control}
                        name="dob"
                        render={({ field }) => (
                        <FormItem>
                            <FormLabel>Date of Birth</FormLabel>
                            <FormControl><Input type="date" {...field} /></FormControl>
                            <FormMessage />
                        </FormItem>
                        )}
                    />
                </div>
            </div>

            {/* Employment Details Section */}
            <div className="space-y-4">
                <h3 className="text-lg font-medium">Employment Details</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <FormField
                    control={form.control}
                    name="position"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Position</FormLabel>
                            <FormControl><Input placeholder="e.g., Software Engineer" {...field} /></FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="department"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Department</FormLabel>
                            <FormControl><Input placeholder="e.g., Technology" {...field} /></FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <FormField
                        control={form.control}
                        name="branch"
                        render={({ field }) => (
                        <FormItem>
                            <FormLabel>Branch</FormLabel>
                            <Select onValueChange={field.onChange} defaultValue={field.value}>
                                <FormControl>
                                    <SelectTrigger><SelectValue placeholder="Select branch" /></SelectTrigger>
                                </FormControl>
                                <SelectContent>
                                    <SelectItem value="hq">Headquarters</SelectItem>
                                    <SelectItem value="downtown">Downtown Office</SelectItem>
                                    <SelectItem value="remote">Remote</SelectItem>
                                </SelectContent>
                            </Select>
                            <FormMessage />
                        </FormItem>
                        )}
                    />
                    <FormField
                        control={form.control}
                        name="hireDate"
                        render={({ field }) => (
                        <FormItem>
                            <FormLabel>Date of Hire</FormLabel>
                            <FormControl><Input type="date" {...field} /></FormControl>
                            <FormMessage />
                        </FormItem>
                        )}
                    />
                </div>
            </div>

            {/* Contact Information Section */}
            <div className="space-y-4">
                <h3 className="text-lg font-medium">Contact Information</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <FormField
                    control={form.control}
                    name="email"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Email Address</FormLabel>
                            <FormControl><Input type="email" placeholder="michael.johnson@example.com" {...field} /></FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="phone"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Phone Number</FormLabel>
                            <FormControl><Input placeholder="+1 555 123 4567" {...field} /></FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                </div>
            </div>
            </CardContent>
            <CardFooter className="flex justify-end">
                <AlertDialog>
                    <AlertDialogTrigger asChild>
                    <Button type="button" disabled={!form.formState.isValid}>Submit for approval</Button>
                    </AlertDialogTrigger>
                    <AlertDialogContent>
                        <AlertDialogHeader>
                            <AlertDialogTitle>Are you sure you want to submit for approval?</AlertDialogTitle>
                            <AlertDialogDescription>
                                This will send the new employee profile to an administrator for approval.
                            </AlertDialogDescription>
                        </AlertDialogHeader>
                        <AlertDialogFooter>
                            <AlertDialogCancel>Cancel</AlertDialogCancel>
                            <AlertDialogAction onClick={form.handleSubmit(handleSubmit)}>Submit for approval</AlertDialogAction>
                        </AlertDialogFooter>
                    </AlertDialogContent>
                </AlertDialog>
            </CardFooter>
        </Card>
      </form>
    </Form>
    </div>
  );
}
